header {
  position: sticky;
  top: 0;
  background-color: #e7eaee;
  z-index: 10;
}

header nav {
  display: flex;
  align-items: center;
  width: 90%;
  margin: auto;
  padding: 2rem;
  min-height: 10vh;
}

header nav #logo {
  flex: 1 1 50rem;
}

.nav-links {
  display: flex;
  justify-content: space-around;
  flex: 1 1 50rem;
  margin: 0;
}

.nav-links li a {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 17px;
  line-height: 26px;
  color: #226500;
  box-shadow: inset 0 0 0 0 #226500;
  padding: 0 0.4rem;
  margin: 0 -0.25rem;
  border-radius: 2px;
  transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.nav-links li .active-link {
  background-color: #226500;
  color: #fff;
  border-radius: 2px;
  padding: 0rem 0.4rem;
}

.nav-links li a:hover {
  color: #fff;
  box-shadow: inset 200px 0 0 0 #226500;
}

.bars-button {
  display: none;
}

@media screen and (max-width: 1024px) {
  .nav-links {
    flex: 1 1 80rem;
  }
}

@media screen and (max-width: 767px) {
  .nav-links {
    display: none;
  }
  .nav-links.actives {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(315deg, #734ae8 0%, #89d4cf 74%);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .nav-links li {
    margin-bottom: 2rem;
  }

  .nav-links li a {
    color: #fff;
  }

  .bars-button {
    position: absolute;
    right: 8%;
    top: 35%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    width: 50px;
    height: 30px;
    cursor: pointer;
  }

  .bars-button div {
    background-color: #226500;
    width: 100%;
    height: 3px;
    transition: all 0.2s linear;
  }

  .bars-button.active div {
    background-color: #fff;
  }

  .bars-button.active {
    transition: all 0.2s linear;
  }

  .bars-button.active:hover {
    transform: rotate(90deg);
  }

  .bars-button.active:hover div {
    background-color: #226500;
  }

  .bars-button.active div:nth-child(2) {
    opacity: 0;
  }
  .bars-button.active div:nth-child(1) {
    transform: rotate(-45deg) translate(-11px, 11px);
  }
  .bars-button.active div:nth-child(3) {
    transform: rotate(45deg) translate(-8px, -8px);
  }
  .nav-links li .active-link {
    background-color: none;
  }
}
